@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{font-family:'Roboto Mono', sans-serif;background-color:#1d1d21}button{cursor:pointer;border:none;outline:none}button:disabled{cursor:default}a{text-decoration:none;color:inherit}.MuiFilledInput-inputMultiline::-webkit-scrollbar{width:6px}.MuiFilledInput-inputMultiline::-webkit-scrollbar-track{background-color:#1d726b;border-radius:4px}.MuiFilledInput-inputMultiline::-webkit-scrollbar-thumb{background:#144944;border-radius:4px}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,textarea:-webkit-autofill,textarea:-webkit-autofill:hover,textarea:-webkit-autofill:focus{-webkit-text-fill-color:#fffff2;caret-color:white;-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s}@-webkit-keyframes spin{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

nav{position:absolute;width:100%;background-color:rgba(15,15,15,0.85);height:60px;display:flex;justify-content:flex-end;align-items:center;font-weight:500;font-size:24px;color:#545454;z-index:1000}.nav__account{display:flex;justify-content:space-between;align-items:center;width:300px;margin-right:15px}.nav__account div{cursor:pointer;-webkit-transition:color 0.15s linear;transition:color 0.15s linear}.nav__account div:hover{color:#ffffff}@media only screen and (max-width: 500px){.nav__account{margin:auto}}

.loading-screen{width:100%;display:flex;justify-content:center;flex-direction:column;align-items:center}.spin{width:100px;height:100px;border:4px solid rgba(255,255,255,0.3);border-radius:50%;border-top-color:#fff;-webkit-animation:spin 1s ease-in-out infinite;animation:spin 1s ease-in-out infinite}

